import React from "react";
import Highlight from "react-highlight";

export const Code1 = () => (
  <Highlight className="python code">
{`# server_responder.py

import responder

api = responder.API()

@api.route("/hello")
async def hello(req, resp):
    resp.text = "Hello World!"

if __name__ == "__main__":
    api.run(port=3000)`}
  </Highlight>
);

