export const Code6 = () => (
  <Highlight className="python code">
{`# server_responder.py

import responder

api = responder.API()

@api.route("/hello/{name}")
async def hello(req, resp, *, name):
    resp.content = api.template("page.html", name=name)

if __name__ == "__main__":
    api.run(port=3000)`}
  </Highlight>
);

